import React, { useState } from "react";

import { Slider } from "react-semantic-ui-range";
import "semantic-ui-css/semantic.min.css";
import { Label, Grid, Input } from "semantic-ui-react";
import ComponentCard from "./ComponentCard";
import ErrorBar from "./ErrorBar";

const Controlled = props => {
  const [value, setValue] = useState(5);
  const [doubleValues, setDoubleValues] = useState([2, 5]);
  const settings = {
    start: 2,
    min: 0,
    max: 10,
    step: 1
  };
  const settingsWithCallback = {
    start: 2,
    min: 0,
    max: 10,
    step: 1,
    onChange: value => {
      setValue(value);
    }
  };
  const handleValueChange = e => {
    let value = parseInt(e.target.value);
    if (!value) {
      value = 0;
    }
    setValue(e.target.value);
  };
  const doubleSettings = {
    start: [1, 6],
    min: 0,
    max: 10,
    step: 1
  };
  const handleDoubleValueChange = (event, index) => {
    const newDoubleValues = [...doubleValues];
    newDoubleValues[index] = parseInt(event.target.value);
    if (!newDoubleValues[index]) {
      newDoubleValues[index] = 0;
    }
    setDoubleValues(newDoubleValues);
  };

  return (
    <Grid>
      <Grid.Column width={16}>
        <ErrorBar message="For external changes use value instead of start. Start is just an initial value for slider, the slider value won't be affected by the changes in the start." />
      </Grid.Column>
      <Grid.Column width={16}>
        <ErrorBar message="If you provide value for the slider, it will override start." />
      </Grid.Column>
      <ComponentCard
        title="Controlled and Callback"
        code={`
        import React, { useState } from "react";
        import { Slider } from "react-semantic-ui-range";
        import { Label, Grid, Input } from "semantic-ui-react";

        const [value, setValue] = useState(5);
        const settings = {
          start: 2,
          min: 0,
          max: 10,
          step: 1,
          onChange: value => {
            setValue(value);
          }
        };

        const handleValueChange = e => {
          let value = parseInt(e.target.value);
          if (!value) {
            value = 0;
          }
          setValue(e.target.value);
        };

        <Slider
          value={value}
          color="red"
          settings={settings}
        />

        <Input placeholder="Enter Value" onChange={handleValueChange} />
        <Label color="red">{value}</Label>`}
      >
        <Slider value={value} color="red" settings={settingsWithCallback} />
        <Input placeholder="Enter Value" onChange={handleValueChange} />
        <Label color="red">{value}</Label>
      </ComponentCard>
      <ComponentCard
        title="Controlled"
        code={`
        import React, { useState } from "react";
        import { Slider } from "react-semantic-ui-range";
        import { Label, Grid, Input } from "semantic-ui-react";

        const [value, setValue] = useState(5);
        const settings = {
          start: 2,
          min: 0,
          max: 10,
          step: 1
        };

        const handleValueChange = e => {
          let value = parseInt(e.target.value);
          if (!value) {
            value = 0;
          }
          setValue(e.target.value);
        };

        <Slider
          value={value}
          color="red"
          settings={settings}
        />

        <Input placeholder="Enter Value" onChange={handleValueChange} />
        <Label color="red">{value}</Label>`}
      >
        <Slider value={value} color="red" settings={settings} />
        <Input placeholder="Enter Value" onChange={handleValueChange} />
        <Label color="red">{value}</Label>
      </ComponentCard>
      <ComponentCard
        title="Multiple Controlled"
        code={`
          import React, { useState } from "react";
          import { Slider } from "react-semantic-ui-range";

          const [doubleValues, setDoubleValues] = useState([2, 5]);
          const doubleSettings = {
            start: [1, 6],
            min: 0,
            max: 10,
            step: 1
          };
          const handleDoubleValueChange = (event, index) => {
            const newDoubleValues = [...doubleValues];
            newDoubleValues[index] = parseInt(event.target.value);
            if (!newDoubleValues[index]) {
              newDoubleValues[index] = 0;
            }
            setDoubleValues(newDoubleValues);
          };        
          
          <Slider
          multiple
          value={doubleValues}
          color="red"
          settings={doubleSettings}
        />
        <Label color="red">{doubleValues[0]}</Label>
        <Label color="red">{doubleValues[1]}</Label>
        <Input
          style={{ display: "inline-block", padding: "0 8px" }}
          placeholder="Enter Value"
          onChange={event => handleDoubleValueChange(event, 0)}
        />
        <Input
          style={{ display: "inline-block" }}
          placeholder="Enter Second Value"
          onChange={event => handleDoubleValueChange(event, 1)}
        />`}
      >
        <Slider
          multiple
          value={doubleValues}
          color="red"
          settings={doubleSettings}
        />
        <Label color="red">{doubleValues[0]}</Label>
        <Label color="red">{doubleValues[1]}</Label>
        <Input
          style={{ display: "inline-block", padding: "0 8px" }}
          placeholder="Enter Value"
          onChange={event => handleDoubleValueChange(event, 0)}
        />
        <Input
          style={{ display: "inline-block" }}
          placeholder="Enter Second Value"
          onChange={event => handleDoubleValueChange(event, 1)}
        />
      </ComponentCard>
    </Grid>
  );
};

export default Controlled;
