import React, { useState } from "react";
import { Slider } from "react-semantic-ui-range";
import { Grid } from "semantic-ui-react";
import "../css/prism.css";
import ErrorBar from "./ErrorBar";
import ComponentCard from "./ComponentCard";
import { Label } from "semantic-ui-react";

const Callback = () => {
  const [value, setValue] = useState(2);
  const [multipleValues, setMultipleValues] = useState([2, 5, 7, 10, 12]);
  const settings = {
    start: 2,
    min: 0,
    max: 20,
    step: 1,
    onChange: value => {
      setValue(value);
    }
  };
  const multipleSettings = {
    start: [2, 5, 7, 10, 12],
    min: 0,
    max: 25,
    step: 1,
    onChange: value => {
      setMultipleValues(value);
    }
  };
  return (
    <Grid>
      <Grid.Column width={16}>
        <ErrorBar message='Start in settings is just an initial value. To change the slider value externally use value instead. For more information open "Controlled" tab.' />
      </Grid.Column>
      <ComponentCard
        title="Normal"
        code={`
        import React, { useState } from "react";
        import { Slider } from "react-semantic-ui-range";
        
        const [value, setValue] = useState(2);
        const settings = {
            start: 2,
            min: 0,
            max: 20,
            step: 1,
            onChange: value => {
              setValue(value);
            }
          };

        <Slider value color="red" settings={settings} />`}
      >
        <Slider color="red" settings={settings} />
        <Label color="red">{value}</Label>
      </ComponentCard>
      <ComponentCard 
      title="Multiple Callback" 
      code={`
        import React, { useState } from "react";
        import { Slider } from "react-semantic-ui-range";
        import { Label } from "semantic-ui-react";
        
        const [multipleValues, setMultipleValues] = useState([2, 5, 7, 10, 12]);
        const settings = {
            start: [2, 5, 7, 10, 12],
            min: 0,
            max: 25,
            step: 1,
            onChange: value => {
              setMultipleValues(value);
            }
          };
          
        <Slider multiple color="red" settings={settings} />

        {multipleValues.map((val, i) => (
          <Label key={i} color="red">
            {val}
          </Label>
        ))}`
      }>
        <Slider multiple color="red" settings={multipleSettings} />
        {multipleValues.map((val, i) => (
          <Label key={i} color="red">
            {val}
          </Label>
        ))}
      </ComponentCard>
    </Grid>
  );
};

export default Callback;
