import React from "react";
import { Slider } from "react-semantic-ui-range";
import { Grid } from "semantic-ui-react";
import ComponentCard from "./ComponentCard";
import ErrorBar from "./ErrorBar";

const Multiple = () => {
  const doubleSettings = {
    start: [2, 4],
    min: 0,
    max: 20,
    step: 1
  };
  const tripleSettings = {
    start: [2, 4, 5],
    min: 0,
    max: 10,
    step: 1
  };
  const multipleSettings = {
    start: [5, 12, 24, 27, 56, 64],
    min: 0,
    max: 70,
    step: 1
  };

  return (
    <Grid>
      <Grid.Column width={16}>
        <ErrorBar message="Provided start array must be in ascending order" />
      </Grid.Column>
      <ComponentCard
        title="Double"
        code={`
        const settings = {
            start: [2, 4],
            min: 0,
            max: 20,
            step: 1
                        };
        <Slider multiple color="red" settings={settings} />
                    `}
      >
        <Slider color="red" multiple settings={doubleSettings} />
      </ComponentCard>
      <ComponentCard
        title="Discrete Double"
        code={`
        const settings = {
            start: [2, 4],
            min: 0,
            max: 20,
            step: 1
                        };
        <Slider discrete multiple color="red" settings={settings} />
                    `}
      >
        <Slider color="red" discrete multiple settings={doubleSettings} />
      </ComponentCard>
      <ComponentCard
        title="Triple"
        code={`
            const settings = {
                start: [2, 4, 5],
                min: 0,
                max: 10,
                step: 1
              };
            <Slider multiple color="red" settings={settings} />
        `}
      >
        <Slider multiple color="red" settings={tripleSettings} />
      </ComponentCard>
      <ComponentCard
        title="Discrete Triple"
        code={`
            const settings = {
                start: [2, 4, 5],
                min: 0,
                max: 10,
                step: 1
              };
            <Slider discrete multiple color="red" settings={settings} />
        `}
      >
        <Slider discrete multiple color="red" settings={tripleSettings} />
      </ComponentCard>
      <ComponentCard
        title="Multiple"
        code={`
            const settings = {
                start: [5, 12, 24, 27, 56, 64],
                min: 0,
                max: 70,
                step: 1
              };
            <Slider multiple color="red" settings={settings} />
        `}
      >
        <Slider multiple color="red" settings={multipleSettings} />
      </ComponentCard>
      <ComponentCard
        title="Discrete Multiple"
        code={`
            const settings = {
                start: [5, 12, 24, 27, 56, 64],
                min: 0,
                max: 70,
                step: 1
              };
            <Slider discrete multiple color="red" settings={settings} />
        `}
      >
        <Slider discrete multiple color="red" settings={multipleSettings} />
      </ComponentCard>
    </Grid>
  );
};

export default Multiple;
