import React, { Component } from "react";
import "semantic-ui-css/semantic.min.css";
import { Segment, Menu, Icon, Label } from "semantic-ui-react";
import Multiple from "./Components/Multiple";
import Basic from "./Components/Basic";
import Colors from "./Components/Colors";
import Style from "./Components/Style";
import Disabled from "./Components/Disabled";
import Callback from "./Components/Callback";
import Controlled from "./Components/Controlled";

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeItem: "basic",
      valueCallback: 4,
      value: 0,
      doubleValues: [1, 5],
      multipleValues: [5, 10, 15, 20],
      multipleDiscreteValues: [2, 10, 16, 19]
    };
  }

  handleItemClick = (e, { name }) => this.setState({ activeItem: name });

  handleDoubleValueChange = (event, index) => {
    const newDoubleValues = [...this.state.doubleValues];
    newDoubleValues[index] = parseInt(event.target.value);
    if (!newDoubleValues[index]) {
      newDoubleValues[index] = 0;
    }
    this.setState({
      doubleValues: newDoubleValues
    });
  };

  render() {
    const { activeItem } = this.state;
    let displayed = <Basic />;
    switch (activeItem) {
      case "basic":
        displayed = <Basic />;
        break;
      case "multiple":
        displayed = <Multiple />;
        break;
      case "colors":
        displayed = <Colors />;
        break;
      case "style":
        displayed = <Style />;
        break;
      case "disabled":
        displayed = <Disabled />;
        break;
      case "callback":
        displayed = <Callback />;
        break;
      case "controlled":
        displayed = <Controlled />;
        break;
      default:
        displayed = <Basic />;
        break;
    }
    return (
      <div>
        <Menu attached="top" tabular>
          <Menu.Item
            name="basic"
            active={activeItem === "basic"}
            onClick={this.handleItemClick}
          />
          <Menu.Item
            name="multiple"
            active={activeItem === "multiple"}
            onClick={this.handleItemClick}
          />
          <Menu.Item
            name="colors"
            active={activeItem === "colors"}
            onClick={this.handleItemClick}
          />
          <Menu.Item
            name="style"
            active={activeItem === "style"}
            onClick={this.handleItemClick}
          />
          <Menu.Item
            name="disabled"
            active={activeItem === "disabled"}
            onClick={this.handleItemClick}
          />
          <Menu.Item
            name="callback"
            active={activeItem === "callback"}
            onClick={this.handleItemClick}
          />
          <Menu.Item
            name="controlled"
            active={activeItem === "controlled"}
            onClick={this.handleItemClick}
          />
          <Menu.Item position="right" name="github">
            <a
              href="https://github.com/iozbeyli/react-semantic-ui-range"
              style={{ textDecoration: "none", color: "black" }}
            >
              <Label color="black">
                <Icon name="github" /> Github
              </Label>
            </a>
          </Menu.Item>
        </Menu>
        <Segment attached="bottom">{displayed}</Segment>
      </div>
    );
  }
}
